@mixin width-height-radius-bgColor($width, $height, $Bradius, $bgColor) {
    width: $width;
    height: $height;
    border-radius: $Bradius;
    background-color: $bgColor;
}

@mixin display-align($display, $align) {
  display: $display;
  align-items: $align;
}

@mixin display-align-justify($display, $align, $justify) {
  display: $display;
  align-items: $align;
  justify-content: $justify;
}

@mixin shimmerBase($keyframes){
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: $keyframes 1.5s infinite;
}
